






























































export default {
	data() {
		return {
			toast:false,
			count:0,
			lifeFruit:'2666',//剩余生命果
			needFruit:'2300',//需要的生命果
			title:'确定兑换商品',
			allFruit:'',//总共需要的生命果
			allfruit:'',//计算后的总生命果
		};
	},
	methods: {
		addNum() {
			this.count++;
		},
		reduce(){
			if(this.count<=0){
				uni.showToast({
					title: '购买数量不能小于0!',
					duration:2000,
					icon:'none'
				});
				this.count=0;
			}else{
				this.count-=1;
			}
		},
		// 兑换商品
		convert(){
			if(this.count==0){
				uni.showToast({
					title: '购买数量不能小于0',
					duration:2000,
					icon:'none'
				});
				return;
			}else{
				this.toast=true;
				this.allFruit=this.count*this.needFruit;
				if(this.allFruit>this.lifeFruit){
					this.title='对不起，生命果不足'
				}else{
					this.title='确定兑换商品'
				}
			}
		},
		// 确定兑换
		confrim(){
			this.toast=false;
		},
		cancel(){
			this.toast=false;
		},
	},
}



